@import url("https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,200&display=swap");

.fa,
.fas {
  font-weight: 900;
  display: grid;
  place-items: center;
  font-size: 1.6rem;
  cursor: pointer;
}

.body {
  width: 80%;
  height: 100vh;
  margin: auto;
}

.header {
  width: 100%;
  line-height: 10rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  border-bottom: 0.1rem solid #9a9191;
  transform: rotate(-0.05deg);
  margin-bottom: 7rem;
}

.continueShopping {
  margin-top: 30px;
  display: flex;
  height: inherit;
  align-items: center;
}

.continueShopping .arrowIcon {
  width: 1.5rem;
  height: 1.5rem;
}

.continueShopping h3 {
  font-size: 1.2rem;
  text-transform: capitalize;
  color: var(--main-color);
  margin-left: 1.5rem;
  font-weight: 400;
}

.cartIcon {
  display: flex;
  text-align: end;
  margin-right: 6rem;
  align-items: center;
  position: relative;
  justify-content: flex-end;
}

.cartIcon .img {
  width: 2rem;
  height: 2rem;
  color: #000000;
}

.cartIcon p {
  position: absolute;
  width: 2rem;
  height: 2rem;
  right: -1.2rem;
  top: 0.9rem;
  border-radius: 50%;
  background: #99cbf7;
  color: #fff;
  box-sizing: border-box;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--main-color);
}

.mainCartSection h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 1.5rem;
  text-transform: capitalize;
  color: rgba(41, 41, 41, 1);
  margin-bottom: 1rem;
  text-align: left;
}

.mainCartSection .totalItems {
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  text-transform: capitalize;
  color: var(--main-color);
  margin-bottom: 5rem;
}

.total-items-count {
  display: inline;
  font-weight: bold;
  color: var(--main-color);
}

/* cart main div start  */

.mainCartSection .cartItems {
  width: 100%;
  height: auto;
  background: linear-gradient(
    103.49deg,
    #ffffff -28.13%,
    rgba(242, 247, 255, 0.5) 116.84%
  );
  box-shadow: rgba(0, 0, 0, 0.08) 0rem 0.4rem 1.2rem;
  border-radius: 2rem;
  margin: auto;
  margin-top: -40px;
  display: grid;
  place-items: center;
}

.cartItems hr {
  margin: 1rem auto;
  overflow-y: scroll;
}

.cartItemsContainer {
  width: 90%;
  height: 30rem;
  /* overflow-y: scroll; */
}

.items-info {
  width: 100%;
  height: 11rem;
  /* background-color: lavender; */
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.items-info .product-img img {
  width: 10rem;
  height: 10rem;
  padding: 10%;
  filter: drop-shadow(0rem 0.4rem 1rem #f1f7ff);
  border-radius: 1rem;
}

.items-info .title {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.items-info .title h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 1rem;
  display: flex;
  margin-left: 10px;
  align-items: center;
  text-transform: capitalize;
  color: var(--main-color);
}

.items-info .title p {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  text-align: left;
  color: black;
}

.add-minus-quantity {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.add-minus-quantity button {
  border: none;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}

.add-minus-quantity input {
  width: 3rem;
  height: 2rem;
  border: 0.5px solid black;
  box-sizing: border-box;
  font-style: normal;
  color: #000000;
  font-size: 1rem;
  text-align: center;
  text-transform: capitalize;
  color: black;
  margin: 0 1rem;
  border-radius: 0.5rem;
  outline: none;
  background-color: transparent;
}

.add-minus-quantity input:focus {
  outline: none;
}

.price {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.price h3 {
  font-style: normal;
  font-weight: bold;
  font-size: 1.2rem;
  text-transform: capitalize;
  color: black;
}

.remove-item {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 5rem;
}

.remove-item button {
  border: none;
  background-color: transparent;
}

.cardTotal {
  width: 95%;
  margin-top: 2rem;
  text-align: right;
  margin-bottom: 2rem;
}

.cardTotal h3 {
  font-style: 200;
  font-size: 1.2rem;
  line-height: 3.2rem;
  color: #000000;
}

.cardTotal h3 span {
  font-style: normal;
  font-weight: bold;
  font-size: 2.8rem;
  line-height: 3.2rem;
  text-transform: capitalize;
  color: #000000;
  margin-left: 1rem;
}

.cardTotal .button {
  border: none;
  font-size: 1rem;
  padding: 1rem 2rem;
  color: #fff;
  background-color: black;
  margin-top: 1rem;
  border-radius: 1rem;
  cursor: pointer;
  letter-spacing: 1px;
  width: 20%;
}

/* hover effects  */
.fa-trash-alt {
  color: #ed4848;
}

.fa-plus:hover {
  color: #000000;
}

.fa-minus:hover {
  color: #000000;
}

.fa-plus,
.fa-minus {
  color: black;
}

.cardTotal .button:hover {
  background-color: #8c8585;
}

.cardTotal .clearCart {
  margin-left: 3rem;
  background-color: rgb(0, 0, 0);
  width: 20%;
}

.cardTotal .clearCart:hover {
  background-color: rgb(0, 0, 0);
}

/* responsive media queries  */

@media (max-width: 968px) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 768px) {
  .continueShopping h3 {
    margin-left: 0;
    font-size: 1.85rem;
  }

  .items-info {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: 1fr;
  }

  .title,
  .add-minus-quantity,
  .price,
  .remove-item {
    padding-left: 2rem;
  }

  .items-info .product-img {
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;
  }

  .add-minus-quantity {
    margin: 2rem 0 2rem 0;
    justify-content: flex-start;
  }

  .price {
    justify-content: flex-start;
    margin-bottom: 2rem;
  }

  .price h3::before {
    content: "Price: ";
  }

  .remove-item {
    justify-content: flex-start;
  }

  .cardTotal {
    margin-bottom: 2rem;
    text-align: center;
  }

  .cardTotal button {
    margin-bottom: 3rem;
  }
}
